.site-header {
  position: relative;
  width: 320px;
  margin: 0 auto;
  outline: 1px solid red; }

.site-header__headline {
  font-size: 25px;
  line-height: 33px;
  font-family: "Playfair Display", "Georgia", "Times New Roman", serif;
  font-weight: 900;
  text-align: center;
  letter-spacing: 1px;
  width: 150px;
  margin: 0 auto 30px;
  padding: 0; }

.main-nav__toggle {
  position: relative;
  top: 0;
  right: 0;
  display: block;
  width: 50px;
  height: 50px;
  margin-bottom: -10px;
  padding: 0;
  border: 0;
  background-color: #ff7931;
  cursor: pointer; }
  .main-nav__toggle .main-nav__cross-logo {
    display: none; }

.main-nav--closed .main-nav__toggle:hover,
.main-nav--closed .main-nav__toggle:focus {
  background-color: rgba(0, 0, 0, 0.7);
  border: solid 2px #ff7931; }

.main-nav--closed .main-nav__toggle:active {
  opacity: 0.3; }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 23px;
  height: 3px;
  background-color: #ffffff;
  box-shadow: 0 6px 0 0 #ffffff, 0 -6px 0 0 #ffffff;
  transform: translate(-50%, -50%); }

.main-nav--closed .main-nav__list {
  display: none; }

.main-nav--opened .main-nav__toggle {
  top: 17px;
  left: 273px;
  width: 30px;
  height: 30px;
  margin-bottom: 46px;
  background-color: #ffffff; }
  .main-nav--opened .main-nav__toggle:hover path,
  .main-nav--opened .main-nav__toggle:focus path {
    stroke: #FF7931; }
  .main-nav--opened .main-nav__toggle:active svg {
    opacity: 0.3; }

.main-nav--opened .main-nav__cross-logo {
  position: absolute;
  top: 0;
  left: 0;
  display: block; }

.site-nav-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.site-nav-list__item {
  margin-bottom: 20px; }
  .site-nav-list__item:last-of-type {
    margin-bottom: 30px; }

.site-nav-list__link {
  position: relative;
  font-size: 16px;
  line-height: 100%;
  color: #48494D;
  text-transform: uppercase;
  letter-spacing: 1.6px;
  display: block;
  padding: 18px 52px 18px 16px;
  background-color: #F9F9FC; }
  .site-nav-list__link::before, .site-nav-list__link::after {
    content: "";
    position: absolute;
    right: 25px;
    top: 50%;
    width: 9px;
    height: 2px;
    background-color: #48494D; }
  .site-nav-list__link::before {
    transform: translateY(-2.5px) rotate(45deg); }
  .site-nav-list__link::after {
    transform: translateY(2.5px) rotate(-45deg); }

.site-nav-list__link[href]:hover, .site-nav-list__link[href]:focus {
  color: #FF7931; }

.site-nav-list__link[href]:active {
  color: rgba(255, 121, 49, 0.3); }

.site-nav-list__link[href]:hover::before, .site-nav-list__link[href]:hover::after, .site-nav-list__link[href]:focus::before, .site-nav-list__link[href]:focus::after {
  background-color: #FF7931; }

.site-nav-list__link[href]:active::before, .site-nav-list__link[href]:active::after {
  background-color: #FF7931; }

.new-article {
  color: #ffffff;
  padding: 25px 0;
  background: url("../../img/hotel.jpg") no-repeat #0a1227;
  background-size: cover; }

.new-article__mark {
  font-size: 16px;
  line-height: 100%;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  display: block;
  width: 100px;
  margin: 0 auto 30px;
  padding: 5px;
  background-color: #ff7931; }

.new-article__title {
  font-size: 30px;
  line-height: 150%;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  width: 280px;
  margin: 0 auto 20px;
  padding: 0; }

.new-article__text {
  font-size: 14px;
  line-height: 150%;
  text-align: center;
  width: 280px;
  margin: 0 auto 30px; }

.slider-controls {
  display: flex;
  width: 110px;
  margin: 0 auto;
  justify-content: space-between; }

.slider-controls__item {
  width: 10px;
  height: 10px;
  padding: 0;
  background-color: #ffffff;
  border: 0;
  border-radius: 50%;
  opacity: 0.4;
  cursor: pointer; }
  .slider-controls__item--active {
    opacity: 1; }

.slider-controls__item[href]:hover, .slider-controls__item[href]:focus {
  opacity: 1; }

.slider-controls__item[href]:active {
  background-color: #FF7931; }
